.slides{
  &__about-author{
    font-style: italic;
  }
}
.slick-track {
  display: flex !important;
}

.slick-slide{
    margin: 0px 20px;
    text-align: center;
     // box-sizing: border-box;
      background-color: #EFF0F3;
      padding: 20px;
      height: inherit !important;
      border-radius: 5px;
      img{
        margin: 0 auto;
      }
}

.slick-dots{
  bottom: -45px !important;
}
.slick-dots li button:before{
  display: block;
  background: #b3b3b3;
  opacity: 1;
  font-size: 0;
  border-radius: 3px;
  font-size: 0 !important;
  width: 16px !important;
  height: 16px !important;
}

.slick-dots li.slick-active button:before{
  display: block;
  background: #FF8E3C;
}

.slider__button{
      position: absolute;
      background: url(../../image/reviews/271228.svg);
      background-repeat: no-repeat;
      background-position: center;
      width: 70px;
      height: 100%;
      border: none;
      outline: none;
      cursor: pointer;
}

.slider__button:hover{
  background-color:#f7f7f7;
}

  .slider__button--back
  {
    top: 0;
    left: 0;
  }

  .slider__button--next
  {
    top: 0;
    right: 0;
    transform: rotate(180deg);
  }

@media(max-width:1024px){
  .slick-slide{
    margin: 0 50px;
  }
}

@media(max-width: 550px){
  .slider__button{
    display: none;
  }
}